import React from "react";
import Slider from "react-slick";
import Movie from "../../movies/Xmassy.mp4"
import GameList from '../GameList';
import HomeBg from '../../images/header.png';

import Image1 from '../../images/XmassyImage_1.png'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
    BrowserRouter as Router,
    Route,
    Link
  } from "react-router-dom";

function Xmassy() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return ( 
    <div>
        <div class="warkBg" style = {{backgroundImage:`url(${HomeBg})`}}>
      <div class = "contentArea">
        <div class = "workPageCard">
          <div class = "workPageBox">
            <Slider {...settings}>
            <div>
              <div class = "videoFrame">
                <video src={Movie} controls></video>
              </div>
            </div>
            <div>
              <div class = "videoFrame">
                <img class = "gameImage" src = {Image1}/>
              </div>
            </div>

            </Slider>

            <div class="info">
            <h2>Xmassy</h2><br/>
            ジャンル：ライティングアクションゲーム<br/>
            プラットフォーム：PC<br/>
            操作：Xboxコントローラ<br/>
            </div>

            <Route  component={GameList} />
          </div>
        </div>
      </div>
      </div>
    </div>
  );
}

export default Xmassy;